/* eslint-disable array-callback-return */
import firebase, {database} from '../../firebase';

export const actionUserName = () => (dispatch) => {
  setTimeout(() => {
    return dispatch({type: 'CHANGE_USER', value: 'Huda Damar'})
  }, 2000);
}

export const registerUser = (data, blankform) => (dispatch) => {
  dispatch({type: 'CHANGE_ISLOADING', value: true})
  return (
     firebase.auth()
            .createUserWithEmailAndPassword(data.email, data.password)
            .then(res => {
              console.log(res)
              dispatch({type: 'CHANGE_ISLOADING', value: false})
              blankform();
              dispatch({type: 'CHANGE_POPUP', value: true})
              setTimeout(() => {
              dispatch({type: 'CHANGE_POPUP', value: false})
              }, 2000);
            })
            .catch(error => {
              let errorCode = error.code;
              let errorMessage = error.message;
              dispatch({type: 'CHANGE_ISLOADING', value: false})
              alert(errorCode, errorMessage)
            })
  )
}

export const loginUser = (data, blankform) => (dispatch) => {
  return new Promise((resolve, reject) => {
    dispatch({type: 'CHANGE_ISLOADING', value: true})
     firebase.auth()
             .signInWithEmailAndPassword(data.email, data.password)
             .then(res => {
                const userData = {
                  email: res.user.email,
                  uid: res.user.uid,
                  emailVerified: res.user.emailVerified,
                  refreshToken: res.user.refreshToken
                }
                dispatch({type: 'CHANGE_ISLOADING', value: false})
                dispatch({type: 'CHANGE_ISLOGIN',   value: true})
                dispatch({type: 'CHANGE_USER',      value: userData})
                resolve(userData)
                console.log(userData)
             })
             .catch(error => {
                let errorCode = error.code;
                let errorMessage = error.message;
                console.log(errorCode, errorMessage)
                blankform();
                dispatch({type: 'CHANGE_ISLOADING', value: false})
                dispatch({type: 'CHANGE_ISLOGIN',   value: false})
                reject('Akun tidak ditemukan, silahkan periksa kembali..')
             })
  })
}

export const addDataAPI = (data) => (dispatch) => {
  database.ref('notes/' + data.userId).push({
    title: data.title,
    content: data.content,
    date: data.date
  })
}

export const getDataAPI = (userID) => (dispatch) => {
  return new Promise((resolve, reject) => {
    database.ref('notes/' + userID)
            .on('value', function  (snapshot){
              const dataNotes = []
                if(snapshot.val()){
                  Object.keys(snapshot.val()).forEach(key => {
                    dataNotes.push({
                      id: key,
                      data: snapshot.val()[key]
                    })
                  });
                  dispatch({type: 'GET_NOTES', value : dataNotes})
                  resolve(snapshot.val())
                }
            }, (error) => {
              reject(error)
            })
  })
}

export const updateDataAPI = (data) => (dispatch) => {
  const putNote = database.ref(`notes/${data.userId}/${data.noteId}`);
  console.log(data.noteId)
  return new Promise((resolve, reject) => {
    putNote.set({
      title: data.title,
      content: data.content,
      date: data.date
    }, (err) => {
      if(err){
        reject(alert(err))
      } else {
        resolve(alert(`data dengan title : ${data.title} , Berhasil di Update`))
      }
    })
           
  })
}

export const deleteDataAPI = (data) => (dispatch) => {
  console.log(data.noteId)
  return new Promise((resolve, reject) => {
    database.ref(`notes/${data.userId}/${data.noteId}`).remove();
  })
}