import React, {Component, Fragment} from 'react';
import './dashboard.scss'
import { addDataAPI, getDataAPI, updateDataAPI, deleteDataAPI} from '../../../Config/redux/action';
import { connect } from 'react-redux';

class Dashboard extends Component{
  constructor(props){
    super(props);
    this.state ={
      title: '',
      content: '',
      date: '',
      noteId: '',
      isUpdate: false
    }
  }

  setDate = (date) => {
    const hari = ['Minggu','Senin', 'Selasa','Rabu','Kamis','Jum\'at','Sabtu'];
    const getDay = new Date(date).getDay()
    const tanggal = new Date(date).getDate()
    const bulan = new Date(date).getMonth() + 1
    const tahun = new Date(date).getFullYear()
    console.log(hari[getDay]);
    return `${hari[getDay ]} | ${tanggal}-${bulan}-${tahun}`;
  }

  componentDidMount = async () => {
    const userData = JSON.parse(localStorage.getItem('userData'));
    if(userData){
      await this.props.getNote(userData.uid);
    } else {
      const dashboard = document.querySelector('.dash-container');
      dashboard.style.flexDirection = 'column';
      dashboard.style.textAlign = 'center'
      dashboard.style.padding = '100px 0'
      dashboard.innerHTML =
      `<h1>404 Not Found</h1>
        <hr/>
       <p>you are not user, please login..</p>
       <a href="/">Oke i'm Back</a>`
    }
  }

  handleInputChange = (e, name) => {
    this.setState({
      [name] : e.target.value
    })
  }

  handleSaveNote = () => {
    const {title, content,isUpdate, noteId} = this.state;
    const {saveNote, updateNote} = this.props;
    const userData = JSON.parse(localStorage.getItem('userData'))
    const data = {
      title  :title,
      content : content,
      userId: userData.uid,
      date: new Date().getTime()
    }
    if(!isUpdate){
      saveNote(data);
    } else {
      data.noteId = noteId;
      updateNote(data).then(() => {
        this.setState({
          title: '',
          content: '',
          isUpdate: false
        })
      });
    }
  }

  updateInput = (note) => {
    console.log(note)
    this.setState({
      title: note.data.title,
      content: note.data.content,
      noteId: note.id,
      isUpdate: true
    })
  }

  handleCancel = () => {
    this.setState({
      title: '',
      content: '',
      isUpdate: false
    })
  }

  deleteNote(note){
    const userData = JSON.parse(localStorage.getItem('userData'))
    const data = {
      userId : userData.uid,
      noteId : note.id
    }
    this.props.deleteNote(data)
  }

  render(){
    const {title, content} = this.state;
    const {notes} = this.props;
    return (
      <div className="dash-container">
          <div className="dash-header">
           <h3>Dashboard pages</h3>
            <input placeholder="title.." className="title" value={title}
                    onChange={(e) => this.handleInputChange(e, 'title')}/>
            <textarea placeholder="content.." className="content" value={content}
                    onChange={(e) => this.handleInputChange(e, 'content')}></textarea>
           {
             this.state.isUpdate
             ? <div className="isupdate">
                <button className="btn-cancel" onClick={this.handleCancel}>Cancel</button>
                <button className="btn-save" onClick={this.handleSaveNote}>Update</button>
             </div>
             :  <button className="btn-save" onClick={this.handleSaveNote}>Save</button>
           }
          </div>
          <div className="dash-notes">
          {
            notes.length > 0
              ? (
                    <Fragment>
                    {
                      notes.map(note => {
                        return (
                          <div className="card-container" key={note.id}>
                          <div className="delete-confirm">
                            <button onClick={(e)=> {
                              e.target.parentElement.nextSibling.style.transform = 'translate(-1000px, 2px)'
                              e.target.parentElement.style.opacity = '0'
                              setTimeout(()=>{
                              this.deleteNote(note)
                              },300)
                            }}>YES</button>
                            <button className="delete-confirm-no" onClick={(e)=> {
                              console.log(e.target.parentElement.nextSibling)
                              e.target.parentElement.nextSibling.classList.remove('delete-slide')
                            }}>NO</button>
                          </div>
                          <div className="dash-card"
                                onClick={(() => this.updateInput(note))}>
                            <div className="dash-title">
                              <h3>{note.data.title}</h3>
                              <p>{this.setDate(note.data.date)}</p>
                            </div>
                            <div className="dash-content">
                              <p>{note.data.content}</p>
                            </div>
                            <div className="btn-delete" onClick={(e) => {
                              e.stopPropagation();
                              e.target.parentElement.classList.add('delete-slide')
                              }}>Delete</div>
                          </div>
                             
                          </div>
                        )
                      } )
                    } 
                    </Fragment>
                    )
              : (<p>anda tidak mempunyai note..</p>)
          }
            
          </div>
        </div>
    )
  }
}

const reduxState = (state) => ({
  userData: state.userData,
  notes: state.notes
})

const reduxDispatch = (dispatch) => ({
  getNote     : (userID) => dispatch(getDataAPI(userID)),
  saveNote    : (data) => dispatch(addDataAPI(data)),
  updateNote  : (data) => dispatch(updateDataAPI(data)),
  deleteNote  : (data) => dispatch(deleteDataAPI(data))
})

export default connect(reduxState, reduxDispatch)(Dashboard);