import React, {Component} from 'react';
import './login.scss';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom';
import { loginUser } from '../../../Config/redux/action';
import Button from '../../../Components/atoms/Button';

class Login extends Component{
  constructor(props){
    super(props);
    this.state ={
      email: '',
      password: ''
    }
  }

  handleInputChange = (e) => {
    this.setState({
      [e.target.id] : e.target.value
    })
  }

  handleLoginSubmit = async (e) => {
    const {email, password} = this.state;
    const blankForm = () => this.setState({password: ''})
    const {history} = this.props
    const res = await this.props.loginFirebaseUser({email, password}, blankForm).catch(err => alert(err))
    if(res){
      localStorage.setItem('userData', JSON.stringify(res))
      history.push('/dashboard')
    }
    
  }

  render(){
    return (
      <div className="auth-container">
      <div className="auth-card">
        <p>Login pages</p>
        {this.props.popup ? <p>Login OKE</p> : null}
        
        <input type="text" id="email" value={this.state.email} placeholder="Email"
                onChange={this.handleInputChange}/>
                
        <input type="password" id="password" value={this.state.password} placeholder="Password"
                onChange={this.handleInputChange}/>
        
        <Button onClick={this.handleLoginSubmit} title="Login" loading={this.props.isLoading}/>
          <span>tidak punya akun? <Link to="/register">register</Link></span>
      </div>
      </div>
    )
  }
}

const reduxState = (state) => ({
  isLoading: state.isLoading,
  popup: state.popup
})

const reduxDispatch = () => (dispatch) => ({
  loginFirebaseUser: (data, blankform) => dispatch(loginUser(data, blankform)),
})

export default connect(reduxState, reduxDispatch)(Login);