import React, {Component} from 'react';
import { connect } from 'react-redux';
import {Link} from 'react-router-dom';
import Button from '../../../Components/atoms/Button';
import { registerUser } from '../../../Config/redux/action';

class Register extends Component{
  constructor(props){
    super(props);
    this.state ={
      email: '',
      password: ''
    }
  }

  handleInputChange = (e) => {
    this.setState({
      [e.target.id] : e.target.value
    })
  }

  handleRegisterSubmit = (e) => {
    const {email, password} = this.state;
    const blankForm = () => this.setState({email: '', password: ''})
    this.props.registerFirebaseUser({email, password}, blankForm)
    
  }

  changeUser = () => {
    this.props.changeUserName();
  }

  render(){
    return (
      <div className="auth-container">
      <div className="auth-card">
        <p>Register {this.props.userName}</p>
        {this.props.popup ? <p>Register OKE</p> : null}
        
        <input type="text" id="email" value={this.state.email} placeholder="Email"
                onChange={this.handleInputChange}/>
                
        <input type="password" id="password" value={this.state.password} placeholder="Password"
                onChange={this.handleInputChange}/>
        
        <Button onClick={this.handleRegisterSubmit} title="Register" loading={this.props.isLoading}/>
        <span>sudah punya akun? <Link to="/">login</Link></span>
      </div>
      </div>
    )
  }
}

const reduxState = (state) => ({
  isLoading: state.isLoading,
  popup: state.popup
})

const reduxDispatch = () => (dispatch) => ({
  registerFirebaseUser: (data, blankform) => dispatch(registerUser(data, blankform)),
})

export default connect(reduxState,reduxDispatch)(Register);